// MyTabBar.js
import React, { useState } from 'react';
import { Tabs, Tab, Form, Button, Dropdown } from 'react-bootstrap';
import AllCollapseExample from './AllCollapseExample';
import { create } from 'xmlbuilder2';

const MyTabBar = () => {
  const [key, setKey] = useState('tab1');
  const [accordionItemsTab1, setAccordionItemsTab1] = useState([]);
  const [accordionItemsTab2, setAccordionItemsTab2] = useState([]);
  const [accordionItemsTab3, setAccordionItemsTab3] = useState([]);
  const [inputTitle, setInputTitle] = useState('Default Title'); // Set your default title
  const [inputField1, setInputField1] = useState('');
  const [inputField2, setInputField2] = useState('');
  const [inputField3, setInputField3] = useState('');
  const [selectedOption, setSelectedOption] = useState('Option 1'); // Add your default option

  const handleAddAccordionItemTab1 = (newItem) => {
    setAccordionItemsTab1([...accordionItemsTab1, newItem]);
  };

  const handleAddAccordionItemTab2 = (newItem) => {
    setAccordionItemsTab2([...accordionItemsTab2, newItem]);
  };

  const handleAddAccordionItemTab3 = (newItem) => {
    setAccordionItemsTab3([...accordionItemsTab3, newItem]);
  };

  const handleRemoveAccordionItemTab1 = (itemId) => {
    setAccordionItemsTab1(accordionItemsTab1.filter((item) => item.id !== itemId));
  };

  const handleRemoveAccordionItemTab2 = (itemId) => {
    setAccordionItemsTab2(accordionItemsTab2.filter((item) => item.id !== itemId));
  };

  const handleRemoveAccordionItemTab3 = (itemId) => {
    setAccordionItemsTab3(accordionItemsTab3.filter((item) => item.id !== itemId));
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();
    // Custom logic for handling form submission
    console.log('Form submitted with the following data:');
    console.log('Input Field Title:', inputTitle);
    console.log('Input Field 1:', inputField1);
    console.log('Input Field 2:', inputField2);
    console.log('Input Field 3:', inputField3);
    console.log('Selected Dropdown Option:', selectedOption);
    console.log('Accordion Items (Tab 1):', accordionItemsTab1);
    console.log('Accordion Items (Tab 2):', accordionItemsTab2);
    console.log('Accordion Items (Tab 3):', accordionItemsTab3);

    

  };
  const launchNotepad = async () => {
    try {
      const response = await fetch('http://localhost:3001/launchNotepad', {
        method: 'GET',
      });
  
      if (response.ok) {
        console.log('Notepad launched successfully');
      } else {
        console.error('Failed to launch Notepad');
      }
    } catch (error) {
      console.error('Error launching Notepad:', error.message);
    }
  };
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    // Perform actions with the uploaded file, e.g., read its content
    console.log('Uploaded file:', file);
  };

  return (
    <Tabs
      id="my-tab-bar"
      activeKey={key}
      onSelect={(k) => setKey(k)}
      className="mb-3"
    >
      <Tab eventKey="tab1" title="Tab 1">
        <Form onSubmit={handleFormSubmit}>
          <Form.Group controlId="formInput">
            <Form.Label>Create Scenario:</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter title"
              value={inputTitle}
              onChange={(e) => setInputTitle(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="formInput1">
            <Form.Label>Input Field 1:</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter text"
              value={inputField1}
              onChange={(e) => setInputField1(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="formInput2">
            <Form.Label>Input Field 2:</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter text"
              value={inputField2}
              onChange={(e) => setInputField2(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="formInput3">
            <Form.Label>Input Field 3:</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter text"
              value={inputField3}
              onChange={(e) => setInputField3(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="dropdown">
            <Form.Label>Dropdown:</Form.Label>
            <Dropdown onSelect={(selected) => setSelectedOption(selected)}>
              <Dropdown.Toggle variant="outline-secondary" id="dropdown-basic">
                {selectedOption}
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item eventKey="Option 1">Option 1</Dropdown.Item>
                <Dropdown.Item eventKey="Option 2">Option 2</Dropdown.Item>
                <Dropdown.Item eventKey="Option 3">Option 3</Dropdown.Item>
                {/* Add more dropdown items as needed */}
              </Dropdown.Menu>
            </Dropdown>
          </Form.Group>

          <AllCollapseExample
            accordionItems={accordionItemsTab1}
            onAddAccordionItem={handleAddAccordionItemTab1}
            onRemoveAccordionItem={handleRemoveAccordionItemTab1}
          />

          <Button variant="primary" type="submit" className="mt-3">
            Submit
          </Button>
           {/* Upload XML File Button */}
           <Form.Group controlId="formFile">
            <Form.Label>Upload XML File:</Form.Label>
            <Form.Control type="file" onChange={handleFileUpload} />
          </Form.Group>
          {/* New Launch button */}
        <Button variant="success" className="mt-3 ml-2" onClick={launchNotepad}>
          Launch
        </Button>
        </Form>
      </Tab>

      <Tab eventKey="tab2" title="Tab 2">
        {/* Content for Tab 2 */}
        <div>
          <h3>Tab 2 Content</h3>
          <p>This is a simple div content for Tab 2.</p>
        </div>
      </Tab>

      <Tab eventKey="tab3" title="Tab 3">
        {/* Content for Tab 3 */}
        <div>
          <h3>Tab 3 Content</h3>
          <p>This is a simple div content for Tab 3.</p>
        </div>
      </Tab>

      {/* Add more tabs as needed */}
    </Tabs>
  );
};

export default MyTabBar;
